
import { TiInput } from '../../../src';
export default {
  name: "demo-input",
  data() {
    return {
      statusList: ["primary", "success", "warn", "error", "info"],
    };
  },
  setup() {
      const inputRef = TiInput.use.ref('myInput');

      return {
          outerClear() {
              inputRef.value.methods.clear();
          },
          outerFocus() {
              inputRef.value.methods.focus();
          }
      };
  }
};
